<template>

  <button
      v-if="showInstallBtn"
      @click="getInstallPwaResult"
      id="installBtn">Install btn</button>
      <h1 v-if="!showInstallBtn">PWA Installed</h1>
</template>

<script>
export default {
  compnents: {},
  data() {
    return {
      installPromtEvent: null,
      deferredPrompt: null,
      showInstallBtn: true
    }
  },
  mounted() {
      window.addEventListener('beforeinstallprompt', (event) => {
        event.preventDefault();
        this.deferredPrompt = event;
        console.log(this.deferredPrompt);
        console.log(this.showInstallBtn);
      });
  },
  methods: {
    async getInstallPwaResult() {
      this.deferredPrompt.prompt();
      const choiceResult = await this.deferredPrompt.userChoice;
      if (choiceResult.outcome === 'accepted') {
        console.log('User is trying to insatall PWA');
        this.showInstallBtn = false;
        localStorage.setItem("install", 'true')
      } else {
        console.log('User is declined PWA');
      }
    }
  },
};

</script>

